.gpt3__navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem 6rem;
}
.gpt3__navbar-links {
  flex: 1;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.gtp3__navbar-link_logo {
  margin-right: 2rem;
}
.gtp3__navbar-link_logo img {
  width: 62.56px;
  height: 16.02px;
}
.gpt3__navbar-links_container {
  display: flex;
  flex-direction: row;
}
.gpt3__navbar-sign {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.gpt3__navbar-links_container p,
.gpt3__navbar-sign p,
.gtp3__navbar-menu_container p {
  color: #fff;
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 18px;
  line-height: 25px;
  text-transform: capitalize;
  margin: 0 1rem;
  cursor: pointer;
}
.gpt3__navbar-sign button,
.gtp3__navbar-menu_container button {
  padding: 0.5rem 1rem;
  background: #ff4820;
  border-radius: 5px;
  font-weight: 500;
  font-size: 18px;
  line-height: 25px;
  cursor: pointer;
  color: #fff;
  font-family: var(--font-family);
  border: none;
  outline: none;
}
.gpt3__navbar-menu {
  margin-left: 1rem;
  display: none;
  position: relative;
}
.gpt3__navbar-menu svg {
  cursor: pointer;
}
.gpt3__navbar-menu_container {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  flex-direction: column;
  text-align: end;
  background: var(--color-footer);
  padding: 2rem;
  position: absolute;
  top: 40px;
  color: #fff;
  font-family: var(--font-family);
  right: 0;
  margin-top: 1rem;
  min-width: 210px;
  border-radius: 5px;
  box-shadow: 0 0 5 rgba(0, 0, 0, 0.2);
}
.gpt3__navbar-menu_container p {
  margin: 1rem 0;
}
@media screen and (max-width: 1050px) {
  .gpt3__navbar-links_container {
    display: none;
  }
  .gpt3__navbar-menu {
    display: flex;
  }
  .gpt3__navbar-menu_container-links-sign {
    display: none;
  }
}
@media screen and (max-width: 550px) {
  .gpt3__navbar {
    padding: 2rem;
  }
  .gpt3__navbar-sign {
    display: none;
  }
  .gpt3__navbar-menu_container {
    top: 20px;
  }
  .gpt3__navbar-menu_container-links-sign {
    /* display: flex; */
    display: block;
    text-align: end;
  }
  .gpt3__navbar-menu_container-links-sign p {
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-transform: capitalize;
    /* margin: 0 1rem; */
    cursor: pointer;
  }
  .gpt3__navbar-menu_container-links-sign button {
    padding: 0.5rem 1rem;
    background: #ff4820;
    border-radius: 5px;
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    cursor: pointer;
    color: #fff;
    font-family: var(--font-family);
    border: none;
    outline: none;
    /* margin-top: 1rem; */
  }
}
